'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Switch } from '@/components/ui/switch'
import { Badge } from '@/components/ui/badge'
import { Card, CardContent } from '@/components/ui/card'
import { Slider } from '@/components/ui/slider'
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle
} from '@/components/ui/dialog'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip'
import { Plus, Trash2, Eye, EyeOff, GripVertical, AlertCircle, CheckCircle, Copy, RotateCcw } from 'lucide-react'
import { toast } from 'sonner'
import { cn } from '@/lib/utils'

// APIKeyå¯¹è±¡ç±»å‹
export interface ApiKeyItem {
    key: string
    name: string
    enabled: boolean
    weight: number
    usageCount?: number
    errorCount?: number
    lastUsed?: number
    lastError?: number
}

// è½®è¯¢ç­–ç•¥
export const keyStrategies = [
    { value: 'round-robin', label: 'è½®è¯¢', desc: 'æŒ‰é¡ºåºä¾æ¬¡ä½¿ç”¨' },
    { value: 'random', label: 'éšæœº', desc: 'éšæœºé€‰æ‹©ä¸€ä¸ªKey' },
    { value: 'weighted', label: 'æƒé‡', desc: 'æ ¹æ®æƒé‡éšæœºåˆ†é…' },
    { value: 'least-used', label: 'æœ€å°‘ä½¿ç”¨', desc: 'ä¼˜å…ˆä½¿ç”¨è°ƒç”¨æ¬¡æ•°æœ€å°‘çš„' },
    { value: 'failover', label: 'æ•…éšœè½¬ç§»', desc: 'æŒ‰é¡ºåºä½¿ç”¨ï¼Œå¤±è´¥ååˆ‡æ¢ä¸‹ä¸€ä¸ª' }
]

interface ApiKeyManagerProps {
    apiKeys: ApiKeyItem[]
    strategy: string
    onChange: (keys: ApiKeyItem[], strategy: string) => void
    // æ˜¯å¦æ˜¾ç¤ºç»Ÿè®¡ä¿¡æ¯
    showStats?: boolean
}

/**
 * å¤šAPIKeyç®¡ç†ç»„ä»¶
 * æ”¯æŒæ·»åŠ /åˆ é™¤/å¯ç”¨ç¦ç”¨/æƒé‡è°ƒæ•´/ç­–ç•¥é€‰æ‹©
 */
export function ApiKeyManager({ apiKeys, strategy, onChange, showStats = false }: ApiKeyManagerProps) {
    const [editDialogOpen, setEditDialogOpen] = useState(false)
    const [editingIndex, setEditingIndex] = useState<number | null>(null)
    const [editForm, setEditForm] = useState<ApiKeyItem>({
        key: '',
        name: '',
        enabled: true,
        weight: 100
    })
    const [showKey, setShowKey] = useState<Record<number, boolean>>({})
    const [batchInput, setBatchInput] = useState('')
    const [batchDialogOpen, setBatchDialogOpen] = useState(false)

    // æ‰“å¼€ç¼–è¾‘å¯¹è¯æ¡†
    const openEditDialog = (index?: number) => {
        if (index !== undefined && apiKeys[index]) {
            setEditingIndex(index)
            setEditForm({ ...apiKeys[index] })
        } else {
            setEditingIndex(null)
            setEditForm({
                key: '',
                name: `Key ${apiKeys.length + 1}`,
                enabled: true,
                weight: 100
            })
        }
        setEditDialogOpen(true)
    }

    // ä¿å­˜ç¼–è¾‘
    const saveEdit = () => {
        if (!editForm.key.trim()) {
            toast.error('è¯·è¾“å…¥API Key')
            return
        }

        const newKeys = [...apiKeys]
        if (editingIndex !== null) {
            newKeys[editingIndex] = editForm
        } else {
            newKeys.push(editForm)
        }
        onChange(newKeys, strategy)
        setEditDialogOpen(false)
    }

    // åˆ é™¤Key
    const deleteKey = (index: number) => {
        const newKeys = apiKeys.filter((_, i) => i !== index)
        onChange(newKeys, strategy)
    }

    // åˆ‡æ¢å¯ç”¨çŠ¶æ€
    const toggleEnabled = (index: number) => {
        const newKeys = [...apiKeys]
        newKeys[index] = { ...newKeys[index], enabled: !newKeys[index].enabled }
        onChange(newKeys, strategy)
    }

    // å¤åˆ¶Key
    const copyKey = (key: string) => {
        navigator.clipboard.writeText(key)
        toast.success('å·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
    }
    const handleBatchImport = () => {
        const lines = batchInput.trim().split('\n').filter(Boolean)
        if (lines.length === 0) {
            toast.error('è¯·è¾“å…¥API Keys')
            return
        }
        const existingKeys = new Set(apiKeys.map(k => k.key.trim()))
        const seenKeys = new Set<string>()
        const uniqueLines: string[] = []
        let duplicateCount = 0

        for (const line of lines) {
            const trimmedKey = line.trim()
            if (!trimmedKey) continue

            if (existingKeys.has(trimmedKey) || seenKeys.has(trimmedKey)) {
                duplicateCount++
                continue
            }

            seenKeys.add(trimmedKey)
            uniqueLines.push(trimmedKey)
        }

        if (uniqueLines.length === 0) {
            toast.error('æ‰€æœ‰Keyéƒ½å·²å­˜åœ¨ï¼Œæ— éœ€å¯¼å…¥')
            return
        }

        const newKeys: ApiKeyItem[] = uniqueLines.map((key, i) => ({
            key,
            name: `Key ${apiKeys.length + i + 1}`,
            enabled: true,
            weight: 100
        }))

        onChange([...apiKeys, ...newKeys], strategy)
        setBatchDialogOpen(false)
        setBatchInput('')

        if (duplicateCount > 0) {
            toast.success(`æˆåŠŸå¯¼å…¥ ${newKeys.length} ä¸ªKeyï¼Œè·³è¿‡ ${duplicateCount} ä¸ªé‡å¤`)
        } else {
            toast.success(`æˆåŠŸå¯¼å…¥ ${newKeys.length} ä¸ªKey`)
        }
    }

    // é‡ç½®é”™è¯¯è®¡æ•°
    const resetErrors = (index: number) => {
        const newKeys = [...apiKeys]
        newKeys[index] = {
            ...newKeys[index],
            errorCount: 0,
            lastError: undefined
        }
        onChange(newKeys, strategy)
        toast.success('å·²é‡ç½®é”™è¯¯è®¡æ•°')
    }

    // é®è”½Keyæ˜¾ç¤º
    const maskKey = (key: string) => {
        if (key.length <= 12) return 'â€¢â€¢â€¢â€¢â€¢â€¢â€¢â€¢'
        return `${key.substring(0, 8)}â€¢â€¢â€¢â€¢${key.slice(-4)}`
    }

    return (
        <div className="space-y-4">
            {/* ç­–ç•¥é€‰æ‹© */}
            <div className="grid gap-2">
                <Label>è½®è¯¢ç­–ç•¥</Label>
                <Select value={strategy} onValueChange={(v: string) => onChange(apiKeys, v)}>
                    <SelectTrigger>
                        <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                        {keyStrategies.map(s => (
                            <SelectItem key={s.value} value={s.value}>
                                <div className="flex flex-col">
                                    <span>{s.label}</span>
                                    <span className="text-xs text-muted-foreground">{s.desc}</span>
                                </div>
                            </SelectItem>
                        ))}
                    </SelectContent>
                </Select>
            </div>

            {/* Keyåˆ—è¡¨ */}
            <div className="space-y-2">
                <div className="flex items-center justify-between">
                    <Label>API Keys ({apiKeys.length})</Label>
                    <div className="flex gap-2">
                        <Button variant="outline" size="sm" onClick={() => setBatchDialogOpen(true)}>
                            æ‰¹é‡å¯¼å…¥
                        </Button>
                        <Button variant="outline" size="sm" onClick={() => openEditDialog()}>
                            <Plus className="h-4 w-4 mr-1" />
                            æ·»åŠ 
                        </Button>
                    </div>
                </div>

                {apiKeys.length === 0 ? (
                    <div className="text-center py-8 text-muted-foreground border rounded-lg border-dashed">
                        æš‚æ— API Keyï¼Œç‚¹å‡»&quot;æ·»åŠ &quot;æŒ‰é’®æ·»åŠ 
                    </div>
                ) : (
                    <div className="space-y-2 max-h-[300px] overflow-y-auto pr-1">
                        {apiKeys.map((item, index) => (
                            <Card key={index} className={cn('transition-opacity', !item.enabled && 'opacity-50')}>
                                <CardContent className="p-3">
                                    <div className="flex items-center gap-3">
                                        {/* æ‹–æ‹½æ‰‹æŸ„ */}
                                        <GripVertical className="h-4 w-4 text-muted-foreground cursor-grab" />

                                        {/* çŠ¶æ€æŒ‡ç¤º */}
                                        <TooltipProvider>
                                            <Tooltip>
                                                <TooltipTrigger>
                                                    {item.errorCount && item.errorCount >= 5 ? (
                                                        <AlertCircle className="h-4 w-4 text-destructive" />
                                                    ) : item.enabled ? (
                                                        <CheckCircle className="h-4 w-4 text-green-500" />
                                                    ) : (
                                                        <div className="h-4 w-4 rounded-full border-2 border-muted-foreground" />
                                                    )}
                                                </TooltipTrigger>
                                                <TooltipContent>
                                                    {item.errorCount && item.errorCount >= 5
                                                        ? `é”™è¯¯æ¬¡æ•°è¿‡å¤š (${item.errorCount})`
                                                        : item.enabled
                                                          ? 'å·²å¯ç”¨'
                                                          : 'å·²ç¦ç”¨'}
                                                </TooltipContent>
                                            </Tooltip>
                                        </TooltipProvider>

                                        {/* Keyä¿¡æ¯ */}
                                        <div className="flex-1 min-w-0">
                                            <div className="flex items-center gap-2">
                                                <span className="font-medium text-sm truncate">{item.name}</span>
                                                {strategy === 'weighted' && (
                                                    <Badge variant="secondary" className="text-xs">
                                                        æƒé‡: {item.weight}
                                                    </Badge>
                                                )}
                                            </div>
                                            <div className="flex items-center gap-2 mt-1">
                                                <code className="text-xs text-muted-foreground font-mono">
                                                    {showKey[index] ? item.key : maskKey(item.key)}
                                                </code>
                                                <Button
                                                    variant="ghost"
                                                    size="icon"
                                                    className="h-5 w-5"
                                                    onClick={() =>
                                                        setShowKey(prev => ({ ...prev, [index]: !prev[index] }))
                                                    }
                                                >
                                                    {showKey[index] ? (
                                                        <EyeOff className="h-3 w-3" />
                                                    ) : (
                                                        <Eye className="h-3 w-3" />
                                                    )}
                                                </Button>
                                                <Button
                                                    variant="ghost"
                                                    size="icon"
                                                    className="h-5 w-5"
                                                    onClick={() => copyKey(item.key)}
                                                >
                                                    <Copy className="h-3 w-3" />
                                                </Button>
                                            </div>

                                            {/* ç»Ÿè®¡ä¿¡æ¯ */}
                                            {showStats && (
                                                <div className="flex gap-3 mt-1 text-xs text-muted-foreground">
                                                    {item.usageCount !== undefined && (
                                                        <span>è°ƒç”¨: {item.usageCount}</span>
                                                    )}
                                                    {item.errorCount !== undefined && item.errorCount > 0 && (
                                                        <span className="text-destructive">
                                                            é”™è¯¯: {item.errorCount}
                                                        </span>
                                                    )}
                                                    {item.lastUsed && (
                                                        <span>
                                                            æœ€åä½¿ç”¨: {new Date(item.lastUsed).toLocaleString()}
                                                        </span>
                                                    )}
                                                </div>
                                            )}
                                        </div>

                                        {/* æ“ä½œæŒ‰é’® */}
                                        <div className="flex items-center gap-1">
                                            {item.errorCount && item.errorCount > 0 && (
                                                <Button
                                                    variant="ghost"
                                                    size="icon"
                                                    className="h-8 w-8"
                                                    onClick={() => resetErrors(index)}
                                                >
                                                    <RotateCcw className="h-4 w-4" />
                                                </Button>
                                            )}
                                            <Switch
                                                checked={item.enabled}
                                                onCheckedChange={() => toggleEnabled(index)}
                                            />
                                            <Button
                                                variant="ghost"
                                                size="icon"
                                                className="h-8 w-8"
                                                onClick={() => openEditDialog(index)}
                                            >
                                                <Eye className="h-4 w-4" />
                                            </Button>
                                            <Button
                                                variant="ghost"
                                                size="icon"
                                                className="h-8 w-8 text-destructive hover:text-destructive"
                                                onClick={() => deleteKey(index)}
                                            >
                                                <Trash2 className="h-4 w-4" />
                                            </Button>
                                        </div>
                                    </div>
                                </CardContent>
                            </Card>
                        ))}
                    </div>
                )}
            </div>

            {/* ç¼–è¾‘å¯¹è¯æ¡† */}
            <Dialog open={editDialogOpen} onOpenChange={setEditDialogOpen}>
                <DialogContent>
                    <DialogHeader>
                        <DialogTitle>{editingIndex !== null ? 'ç¼–è¾‘ API Key' : 'æ·»åŠ  API Key'}</DialogTitle>
                        <DialogDescription>é…ç½®API Keyçš„åç§°ã€å¯†é’¥å’Œæƒé‡</DialogDescription>
                    </DialogHeader>

                    <div className="space-y-4 py-4">
                        <div className="grid gap-2">
                            <Label htmlFor="keyName">åç§°</Label>
                            <Input
                                id="keyName"
                                value={editForm.name}
                                onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                                    setEditForm({ ...editForm, name: e.target.value })
                                }
                                placeholder="Key 1"
                            />
                        </div>

                        <div className="grid gap-2">
                            <Label htmlFor="keyValue">API Key</Label>
                            <Input
                                id="keyValue"
                                type="password"
                                value={editForm.key}
                                onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                                    setEditForm({ ...editForm, key: e.target.value })
                                }
                                placeholder="sk-..."
                            />
                        </div>

                        <div className="grid gap-2">
                            <div className="flex justify-between">
                                <Label>æƒé‡</Label>
                                <span className="text-sm text-muted-foreground">{editForm.weight}</span>
                            </div>
                            <Slider
                                value={[editForm.weight]}
                                min={1}
                                max={100}
                                step={1}
                                onValueChange={(v: number[]) => setEditForm({ ...editForm, weight: v[0] })}
                            />
                            <p className="text-xs text-muted-foreground">
                                æƒé‡è¶Šé«˜ï¼Œåœ¨&quot;æƒé‡&quot;ç­–ç•¥ä¸‹è¢«é€‰ä¸­çš„æ¦‚ç‡è¶Šå¤§
                            </p>
                        </div>

                        <div className="flex items-center justify-between">
                            <Label>å¯ç”¨</Label>
                            <Switch
                                checked={editForm.enabled}
                                onCheckedChange={(checked: boolean) => setEditForm({ ...editForm, enabled: checked })}
                            />
                        </div>
                    </div>

                    <DialogFooter>
                        <Button variant="outline" onClick={() => setEditDialogOpen(false)}>
                            å–æ¶ˆ
                        </Button>
                        <Button onClick={saveEdit}>ä¿å­˜</Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>

            {/* æ‰¹é‡å¯¼å…¥å¯¹è¯æ¡† */}
            <Dialog open={batchDialogOpen} onOpenChange={setBatchDialogOpen}>
                <DialogContent>
                    <DialogHeader>
                        <DialogTitle>æ‰¹é‡å¯¼å…¥ API Keys</DialogTitle>
                        <DialogDescription>æ¯è¡Œè¾“å…¥ä¸€ä¸ªAPI Key</DialogDescription>
                    </DialogHeader>

                    <textarea
                        className="w-full h-40 p-3 border rounded-md font-mono text-sm resize-none"
                        value={batchInput}
                        onChange={e => setBatchInput(e.target.value)}
                        placeholder="sk-key1...&#10;sk-key2...&#10;sk-key3..."
                    />

                    <DialogFooter>
                        <Button variant="outline" onClick={() => setBatchDialogOpen(false)}>
                            å–æ¶ˆ
                        </Button>
                        <Button onClick={handleBatchImport}>å¯¼å…¥</Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>
        </div>
    )
}

export default ApiKeyManager
